import styled from 'styled-components';

import { IStylesBase } from '@app/palette';
import { IStyles } from '@components/Card/interfaces';
import { IColors } from '@palette/interfaces';

const breakpointFromTablet = ({ theme }: IStylesBase): string => theme.media.tablet;
const breakpointOnlyMobile = ({ theme }: IStylesBase): string => theme.media.onlyMobile;
const setCircleColor = ({ theme, color }: IStyles): string => theme.colors[color as keyof IColors];
const setImageURL = ({ url }: IStyles): string | undefined => url;
const setColor = ({ theme }: IStyles): string => theme.colors.purple;

export const Link = styled.a`
  margin-bottom: 16px;
  width: 450px;
  height: 300px;
  //box-shadow: 11px 11px 21px -5px rgba(4, 4, 4, 0.29);
  transition: transform 250ms linear;

  @media only screen and (max-width: 1367px) and (max-height: 1366px) and (-webkit-min-device-pixel-ratio: 1.5) {
    width: 390px;
    height: 250px;
  }

  @media ${breakpointFromTablet} {
    width: calc(33% - 14px);
    margin-right: 16px;

    &:hover {
      transform: scale(1.03);
    }
  }

  &:nth-child(3n) {
    margin-right: 0;
  }
`;

export const Text = styled.div`
  padding: 4px 8px;
  background-color: rgba(0, 0, 0, 0.2);

  span {
    color: white;
  }
`;

export const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-image: url(${setImageURL});
  background-size: 100% 100%;

  &:hover {
    ${Text} {
      display: none;
    }
  }

  @media ${breakpointFromTablet} {
    border-radius: 8px;
  }
`;

export const Circle = styled.div<IStyles>`
  margin-right: 8px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: ${setCircleColor};
`;

export const Description = styled.div`
  padding: 4px 8px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: ${setColor};

  span {
    color: white;
  }
`;

export const Container = styled.div`
  display: flex;
  align-items: center;
`;
