import { FormikValues, useFormik } from 'formik';
import React, { ReactElement, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import AppsIcon from '@mui/icons-material/Apps';
import SearchIcon from '@mui/icons-material/Search';

import { IStore } from '@app/store';
import API from '@app/utils';
import Input from '@components/Input';
import Select from '@components/Select';
import { IForm } from '@pages/Layout/components/Search/interfaces';
import { DEFAULT_FORM_VALUES, normalizeSearchRequest } from '@pages/Layout/components/Search/utils';
import { actions } from '@store/user';
import { IState } from '@store/user/interfaces';
import { searchVideos } from '@store/videos';
import { IUserResponse } from '@utils/interfaces';

import {
  Wrapper,
  Container,
  Avatar,
  Name,
  Form,
  BaseSection,
  Params,
  Submit,
  Options,
  OptionInput,
} from './styles';

const Header = (): ReactElement => {
  const dispatch = useDispatch();
  const getUser = (store: IStore): IState => store.user;

  const {
    session: {
      user: { id },
    },
    user: { photo_big: avatarURL, first_name: firstName, last_name: lastName },
  } = useSelector(getUser);

  const handleGetUserInfo = (): void => {
    API.getUser(id, handleSetUserInfo);
  };

  const handleSetUserInfo = ({ response: [userInfo] }: IUserResponse): void => {
    userInfo && dispatch(actions.setUserData(userInfo));
  };

  useEffect(handleGetUserInfo, [id]);

  const [isOptions, setIsOptions] = useState<boolean>(false);

  const onSubmit = (values: FormikValues): void => {
    dispatch(searchVideos(normalizeSearchRequest(values as IForm)));
  };

  const form = {
    initialValues: DEFAULT_FORM_VALUES,
    onSubmit,
  };

  const {
    values: { search, longer, shorter, sorting },
    handleChange,
    handleSubmit,
    submitForm,
  } = useFormik(form);

  return (
    <Wrapper>
      <BaseSection>
        <Container>
          <Avatar src={avatarURL} />
          <Name>
            {firstName} {lastName}
          </Name>
        </Container>
        <Form onSubmit={handleSubmit}>
          <Input
            autoFocus={true}
            label="Поиск"
            id="search"
            name="search"
            value={search}
            onChange={handleChange}
          />
          <Params onClick={() => setIsOptions(!isOptions)}>
            <AppsIcon />
          </Params>
          <Submit type="submit">
            <SearchIcon />
          </Submit>
          {isOptions && (
            <Options>
              <OptionInput
                label="От"
                id="longer"
                name="longer"
                value={longer}
                onChange={handleChange}
                placeholder="Введите число"
              />
              <OptionInput
                label="До"
                id="shorter"
                name="shorter"
                value={shorter}
                onChange={handleChange}
                placeholder="Введите число"
              />
              <Select
                labelId="demo-simple-select-label"
                id="sorting"
                name="sorting"
                value={sorting}
                label="Сортировать"
                onChange={handleChange}
                options={[
                  { label: 'по дате', value: 0 },
                  { label: 'по длительности', value: 1 },
                  { label: 'по релевантности', value: 2 },
                ]}
              />
            </Options>
          )}
        </Form>
      </BaseSection>
    </Wrapper>
  );
};

export default Header;
