import styled, { keyframes } from 'styled-components';

import { IStylesBase } from '@app/palette';
import Input from '@components/Input';

const breakpointFromTablet = ({ theme }: IStylesBase): string => theme.media.tablet;
const breakpointOnlyMobile = ({ theme }: IStylesBase): string => theme.media.onlyMobile;
const setBGGreyDark = ({ theme }: IStylesBase): string => theme.colors.greyDark;
const setBGGreyLight = ({ theme }: IStylesBase): string => theme.colors.greyLight;

const show = keyframes`
  from {
    transform: translateY(146%);
  }
  
  to  {
    transform: translateY(120%);
  }
`;

export const Wrapper = styled.div`
  background-color: #fff;
  position: fixed;
  width: 100%;
  z-index: 3;

  @media ${breakpointFromTablet} {
    padding: 12px;
  }
`;

export const BaseSection = styled.section`
  padding: 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 65px;
  background-color: ${setBGGreyDark};
  box-shadow: 11px 11px 21px -5px rgba(4, 4, 4, 0.29);

  @media ${breakpointFromTablet} {
    border-radius: 8px;
  }
`;

export const Container = styled.div`
  display: flex;
  align-items: center;
`;

export const Avatar = styled.img`
  margin-right: 16px;
  width: 25px;
  height: 25px;
  border-radius: 38%;
`;

export const Name = styled.p`
  margin: 0;
  padding: 0;
  font-size: 18px;
  color: white;

  @media ${breakpointOnlyMobile} {
    display: none;
  }
`;

export const Form = styled.form`
  display: flex;
  align-items: center;
`;

export const Params = styled.div`
  flex-shrink: 0;
  margin-left: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 8px;
  background-color: #fff;
  cursor: pointer;

  &:hover {
    background-color: ${setBGGreyLight};
  }
`;

export const Submit = styled.button`
  flex-shrink: 0;
  margin-left: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 8px;
  background-color: #fff;
  cursor: pointer;
  border: none;

  &:hover {
    background-color: ${setBGGreyLight};
  }
`;

export const Options = styled.div`
  position: absolute;
  left: 0;
  right: 0;
  padding: 18px 12px;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: ${setBGGreyDark};
  border-radius: 12px 12px 0 0;
  animation: ${show} 200ms forwards;

  @media ${breakpointFromTablet} {
    top: 85px;
    left: initial;
    right: 12px;
    height: 300px;
    width: 300px;
    border-radius: 12px;
  }
`;

export const OptionInput = styled(Input)`
  width: 100%;
  margin-bottom: 22px !important;
`;
